<script lang="ts">
import Key from "./Key.svelte";
import { AppContext, appContextKey } from "../../AppContext";
import { getContext, onDestroy, onMount } from "svelte";
import { processKeyInput } from "./ProcessKeyInput";
import { BACKSPACE_KEY, BACKSPACE_KEY_CODE, ENTER_KEY, ENTER_KEY_CODE, keyboardKeys, keyboardKeySet } from "./Constants";

const appContext: AppContext = getContext(appContextKey);

onMount(() => {
    window.addEventListener('keyup', onKeyboardPress);
});

onDestroy(() => {
    window.removeEventListener('keyup', onKeyboardPress);
})

const onKeyboardPress = (event: KeyboardEvent) => {
    event.stopPropagation();
    const key = event.key.toUpperCase();
    if (keyboardKeySet.has(key)) {
        onKeyPress(key);
    }
    if (key == ENTER_KEY_CODE) {
        onKeyPress(ENTER_KEY);
    }
    if (key == BACKSPACE_KEY_CODE) {
        onKeyPress(BACKSPACE_KEY)
    }
    
}

const onKeyPress = async (keyValue: string) => {
    processKeyInput(keyValue, appContext)
}
	
</script>

<div class="web-keyboard">
    {#each keyboardKeys as row, rowIdx}
        <div class="row" class:center={rowIdx === 1}>
            {#each row as key}
                <Key keyValue={key} {onKeyPress}/>
            {/each}
        </div>
    {/each}
</div>

<style>
	.web-keyboard {
        display: flex;
        flex-grow: 1;
        flex-flow: column;
        justify-content: space-evenly;
        align-items: center;
    }

    .row {
        display: flex;
        justify-content: space-evenly;
        width: 100%;
    }

    .center {
        width: 90%;
    }
</style>