export const ENTER_KEY = "Enter";
export const BACKSPACE_KEY = "&#9003;";
export const ENTER_KEY_CODE = "ENTER";
export const BACKSPACE_KEY_CODE = "BACKSPACE";
export const keyboardKeys = [
    ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"],
    ["A", "S", "D", "F", "G", "H", "J", "K", "L"],
    [ENTER_KEY, "Z", "X", "C", "V", "B", "N", "M", BACKSPACE_KEY]
];
export const keyboardKeySet = new Set(keyboardKeys.reduce((acc, row) => {
    acc.push(...row);
    return acc;
}, []));
